import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service'
import {AuthService} from '../../services/auth.service'
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: String;
  username: String;
  email: String;
  password: String;
  cpassword: String;
  check: boolean;
  checkP:boolean;
  usertype: String;
  requestusertype: String;

  constructor(private validateService: ValidateService, 
    private flashMessagesService:FlashMessagesService,
    private authService:AuthService,
    private router: Router
  ) { }

  ngOnInit() {
    if(this.authService.loggedIn()){
      //this.router.navigate(['summary']);
    }
    this.checkP=true;

  }

  confirmPass(){
    if(this.password===this.cpassword){
      this.checkP=true;
      return true;
    }else{
      this.checkP=false;
      return false;
    }
    
  }

  checkUserName(){
    const user = {
      username:this.username,
    }
   

    this.authService.findUser(user).subscribe(data=>{
      if(data.success){
       
        this.check=true;
        return true;
      }
      else{
      
        this.check=false;
        return false;
      }
    });
  }



  onRegisterSubmit(){
    const user = {
      name: this.name,
      username:this.username,
      email: this.email,
      password:this.password,
      requestusertype:this.usertype
    }

    

    if(!this.validateService.validateRegister(user)){
      this.flashMessagesService.show('Please fill all fields',{cssClass: 'alert-danger',timeout:3000});
      return false;
    }

    if(!this.validateService.vaidateEmail(user.email)){
      this.flashMessagesService.show('Please use a valid email',{cssClass: 'alert-danger',timeout:3000});
      return false;
    }

    this.authService.registerUser(user).subscribe(data =>{
      if(data.sucess){
        this.flashMessagesService.show('Sucessfully registered!',{cssClass: 'alert-sucess',timeout:3000});
        this.router.navigate(['/login']);
      }
      else{
        this.flashMessagesService.show('Something went wrong!!',{cssClass: 'alert-danger',timeout:3000});
        this.router.navigate(['/register']);
      }
    });

  }

}



