import { Component, OnInit } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { LogService } from '../../services/log.service'
import {FlashMessagesService} from 'angular2-flash-messages';

@Component({
  selector: 'app-upload-log-file',
  templateUrl: './upload-log-file.component.html',
  styleUrls: ['./upload-log-file.component.css']
})
export class UploadLogFileComponent implements OnInit {
  filesToUpload: Array<File>;
  input:any;

  constructor(private http: Http, private logService: LogService,private flashMessagesService:FlashMessagesService) { }

  ngOnInit() {
    this.filesToUpload = [];
  }

  openFile(event) {
    this.input = event.target;

}

upload(){
  for (var index = 0; index < this.input.files.length; index++) {
    let reader = new FileReader();
    reader.onload = () => {
        // this 'text' is the content of the file
        var text = reader.result;
        this.logService.saveLog(text).subscribe(data=>{
          console.log(data);
          if(data.success){
            this.flashMessagesService.show(data.msg,{cssClass: 'alert-success',timeout:3000});
          }
          else{
            this.flashMessagesService.show(data.msg,{cssClass: 'alert-danger',timeout:3000});
          }
          if(data.ok==false){
            this.flashMessagesService.show("Log not saved!",{cssClass: 'alert-danger',timeout:3000});
          }
        });
        
    }
    reader.readAsText(this.input.files[index]);
};

}

}



