import { Component, ElementRef, NgZone, OnDestroy, OnInit, Input,ViewChild } from '@angular/core';

import Chart from 'chart.js';

@Component({
  selector: 'app-scatterchart',
  templateUrl: './scatterchart.component.html',
  styleUrls: ['./scatterchart.component.css']
})
export class ScatterchartComponent implements OnInit {
  @ViewChild('graphcanvas3') mycanvas3:ElementRef;
  @Input() yLabels:Array<any>;
  @Input() xLabels:Array<any>;
  @Input() dataSet1:Array<any>;
  @Input() dataSet2:Array<any>;

  mychart:Chart;

  constructor() { }

  ngOnChanges(){   
    
    if(this.yLabels  && this.xLabels && this.dataSet1 && this.dataSet2){
        
    
        
        this.draw();
        
    }
    //setTimeout(()=>{this.draw();},500);
  }

  ngOnInit() {}

  draw(){
    
    
    if (this.mychart) {
			this.mychart.destroy();
    }
    
     
    let ctx = this.mycanvas3.nativeElement.getContext('2d');
    var chartColors = {
      red: 'rgb(255, 99, 132)',
      orange: 'rgb(255, 159, 64)',
      yellow: 'rgb(255, 205, 86)',
      green: 'rgb(75, 192, 192)',
      blue: 'rgb(54, 162, 235)',
      purple: 'rgb(153, 102, 255)',
      grey: 'rgb(231,233,237)'
    };
    
    this.mychart=new Chart(ctx,
    {
      type: 'line',
      data: {
          xLabels: this.xLabels,
          yLabels: this.yLabels,
          datasets: [{
              label: "Transaction Errors",
              data: this.dataSet1,
              fill: false,
              showLine: false,
              borderColor: chartColors.red,
              backgroundColor: chartColors.red
          },{
            label: "Other Errors",
            data: this.dataSet2,
            fill: false,
            showLine: false,
            borderColor: chartColors.blue,
            backgroundColor: chartColors.blue
        }]
      },
      options: {
          responsive: true,
          title:{
              display:true,
              text:'Error Timeline'
          },
          scales: {
              xAxes: [{
                  display: true,
                  scaleLabel: {
                      display: true,
                      labelString: 'Time'
                  },
                  ticks: {
                    min: 0, // it is for ignoring negative step.
                    beginAtZero: true,
                  }
              }],
              yAxes: [{
                  type: 'category',
                  position: 'left',
                  display: true,
                  scaleLabel: {
                      display: true,
                      labelString: 'Error'
                  },
                  ticks: {
                  }
              }]
          }
      }
  });    

  }

}



