import { Component, ElementRef, NgZone, OnDestroy, OnInit, Input,ViewChild,AfterViewInit } from '@angular/core';
import Chart from 'chart.js';


@Component({
  selector: 'app-barchart',
  templateUrl: './barchart.component.html',
  styleUrls: ['./barchart.component.css']
})
export class BarchartComponent implements OnInit,AfterViewInit {
  @ViewChild('graphcanvas2') mycanvas2:ElementRef;
  @Input() labels:Array<any>;
  @Input() data:Array<any>;
  mychart:Chart;

  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(){
    if(this.labels  && this.data){
			
		
			this.draw();
			
		}
    
  }

  ngAfterViewInit() {
    this.draw();
  }

  draw(){
    if (this.mychart) {
			this.mychart.destroy();
    }
    
    let ctx = this.mycanvas2.nativeElement.getContext('2d');
    
    this.mychart=new Chart(ctx, {
      type: 'bar',
      data: {
        labels: this.labels,
        datasets: [
          {
            label: "No of Errors",
            backgroundColor: ["#3e95cd", "#8e5ea2","#3cba9f","#e8c3b9","#c45850"],
            data: this.data
          }
        ]
      },
      options: {
        legend: { display: false },
        title: {
          display: true,
          text: 'User Errors'
        },
        scales:{
          yAxes:[{
            ticks:{
              min: 0, // it is for ignoring negative step.
							beginAtZero: true
            }
          }]
        }
      }
  });
  }

}



