import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { LogService } from '../../services/log.service';

@Component({
  selector: 'app-home',
  templateUrl: './dash.component.html',
  styleUrls: ['./dash.component.css']
})


export class DashComponent implements OnInit {
  user: any;
  selLogName: string = "test";
  logNames: Object[];
  logLocations: Object[];
  selDate: string;
  selDateO: Date;
  selLocation: string = "Any";
  isAdmin=false;
  username:String;

  @ViewChild('root') root;

  constructor(private authService: AuthService,
    private router: Router,
    private flashMessagesService: FlashMessagesService, private logService: LogService) { }

  ngOnInit() {
    
    this.isAdmin=this.authService.isAdmin();    
    this.username=this.authService.getUserName();
    //console.log(this.username);
    this.logService.getLogNames().subscribe(data => {
      this.logNames = data.log;

    });
    this.logService.currentLog.subscribe(currentLog => {
      this.selLogName = currentLog;
    });
    this.logService.getLogLocations().subscribe(data => {
      this.logLocations = data.log;
    });

  }

  onLogoutClick() {
    this.authService.logout();
    this.flashMessagesService.show('You are logged out', {
      cssClass: 'alert-success', timeout: 3000
    });
    this.router.navigate(['/home']);
    return false;
  }

  changeLog() {
    this.logService.changeLog(this.selLogName);
  }


  changeLogList() {
    if (this.selLocation == "Any" && !this.selDate) {

      this.logService.getLogNames().subscribe(data => {
        this.logNames = data.log;
        this.changeSelLog();
      });
    }
    else if (this.selLocation == "Any" && this.selDate) {

      this.logService.getLogNamesByDate(this.selDate).subscribe(data => {
        this.logNames = data;
        this.changeSelLog();
      });
    }
    else if (this.selLocation != "Any" && !this.selDate) {

      this.logService.getLogNamesByLocation(this.selLocation).subscribe(data => {
        this.logNames = data;
        this.changeSelLog();
      })
    }
    else {

      this.logService.getLogNamesByLocationDate(this.selDate, this.selLocation).subscribe(data => {
        this.logNames = data;
        this.changeSelLog();
      })
    }


  }

  changeSelLog() {
    this.selLogName = this.logNames[0]['logName'];
    this.changeLog();
  }

  fixui(){
    var elem=document.getElementById("ro");
    elem.classList.toggle("fix-scrolly");
    console.log("dsadasdassdasd");
  }

}


