import { Component, ElementRef, NgZone, OnDestroy, OnInit, Input,ViewChild, AfterViewInit  } from '@angular/core';

import Chart from 'chart.js';

@Component({
  selector: 'app-linechart',
  templateUrl: './linechart.component.html',
  styleUrls: ['./linechart.component.css'],
})
export class LinechartComponent implements OnInit,AfterViewInit {
	@ViewChild('graphcanvas') mycanvas:ElementRef;
  
	@Input() labelY:string;
	@Input() dataSet:Array<any>;
	@Input() labels:Array<any>;

	mychart:Chart;
	constructor(){
		
	}
  ngOnChanges(){
	  console.log(this.dataSet);
		if(this.labelY  && this.dataSet){


			if(this.dataSet.length){

				this.draw();
			}
		}
		
	}
	
  ngOnInit() {
		//console.log(this.dataSet.length);
	}
	
	ngAfterViewInit() {
	}

  draw(){
		if (this.mychart) {
			this.mychart.destroy();
		}
		let ctx = this.mycanvas.nativeElement.getContext('2d');
    this.mychart=new Chart(ctx,
      {type: 'line',
			data: {
				labels:this.labels,
				datasets: this.dataSet
			},
			options: {
				responsive: true,
				elements: {
					line: {
							tension: 0 // disables bezier curves
					}
				},
				
				scales: {
					xAxes: [{
						type: "time",
						display: true,
						scaleLabel: {
							display: true,
							labelString: 'Time'
						},
                        ticks: {
                            major: {
                                fontStyle: "bold",
                                fontColor: "#FF0000"
                            }
                        }
					}],
					yAxes: [{
						display: true,					
						scaleLabel: {
							display: true,
							labelString: this.labelY
						},
						ticks: {
							min: 0, // it is for ignoring negative step.
							beginAtZero: true,
							stepSize: 1  // if i use this it always set it '1', which look very awkward if it have high value  e.g. '100'.
					}
					}]
				}
			}
          
        });    
  }
}



