import { Component, OnInit } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { LogService } from '../../services/log.service'

class Error{
  name:string;
  time:Date;
  type:string;
  constructor(name:string,time:Date,type:string){
    this.name=name;
    this.time=time;
    this.type=type;
  }
}
@Component({
  selector: 'app-errors-view',
  templateUrl: './errors-view.component.html',
  styleUrls: ['./errors-view.component.css']
})
export class ErrorsViewComponent implements OnInit {
  noTechnicalErrors: any;
  selLogName: string;
  noTechnicalTransactionErrors:number;
  technicalTransactionErrors:Array<any>
  otherTechnicalErrors:Array<any>;
  logDateOFrom:Date;
  logDateOTo:Date;

  noUserErrors:number;
  barLabels:Array<any>;
  barData:Array<any>;
  
  showTechnicalErrors:boolean;
  showUserErrors:boolean;

  allErrorsf:Array<Error>;
  allErrors:Array<Error>;
  allErrors2:Array<Error>;
  allErrors3:Array<Error>;
  allErrorTimes:Array<any>;
  yLabels:Array<string>=[];
  yLabels2:Array<string>=[];
  yLabels3:Array<string>=[];
  tedataset:Array<any>=[];
  oedataset:Array<any>=[];
  constructor(private http: Http, private logService: LogService) { }

  ngOnInit() {
    this.showTechnicalErrors=true;
    this.showUserErrors=true;

    this.allErrorTimes=[];
    this.logService.currentLog.subscribe(currentLog=>{
      
      this.allErrorTimes=[];
      this.yLabels=[];
      this.yLabels2=[];
      this.yLabels3=[];
      this.allErrors=[];
      this.allErrorsf=[];
      this.allErrors2=[];
      this.allErrors3=[];
      this.tedataset=[];
      this.oedataset=[];
      this.otherTechnicalErrors=[];
      this.selLogName = currentLog;

      this.logService.getLogInfo(currentLog).subscribe(data=>{
        this.logDateOFrom=new Date(data[0].timeValidFrom);
        this.logDateOTo=new Date(data[0].timeValidTo);
        
        this.allErrors.push(new Error(null,this.logDateOFrom,"none"));
        this.allErrors.push(new Error(null,this.logDateOTo,"none"));
        
      });

      
      //this.allErrors.push(new Error(null,this.logDateO,"none"));
      

      this.logService.getNoTechnicalErrors(currentLog).subscribe(data=>{
        this.noTechnicalErrors=data[0].count;
        if(this.noTechnicalErrors>0){
          this.showTechnicalErrors=true;
        }
        else{
          this.showTechnicalErrors=false;
        }
      });
      this.logService.getNoTransactionTechnicalErrors(currentLog).subscribe(data=>{
        this.noTechnicalTransactionErrors=data[0].count;
      });
      this.logService.getTransactionTechnicalErrors(currentLog).subscribe(data=>{
        
        this.technicalTransactionErrors=data;
        this.yLabels.push(" ");
        for(var i=0;i<this.technicalTransactionErrors.length;i++){
          this.technicalTransactionErrors[i].transactions.time=new Date(this.technicalTransactionErrors[i].transactions.time);
          this.allErrors2.push(new Error(this.technicalTransactionErrors[i].transactions.error.desc,new Date(this.technicalTransactionErrors[i].transactions.time),"transaction"));
          if(this.yLabels.indexOf(this.technicalTransactionErrors[i].transactions.error.desc)==-1){
            this.yLabels.push(this.technicalTransactionErrors[i].transactions.error.desc);
          }
        }

        this.logService.getOtherTechnicalErrors(currentLog).subscribe(data=>{
          
          this.otherTechnicalErrors=data;
          for(var i=0;i<this.otherTechnicalErrors.length;i++){
            this.otherTechnicalErrors[i].terrors.time=new Date(this.otherTechnicalErrors[i].terrors.time);
            this.allErrors3.push(new Error(this.otherTechnicalErrors[i].terrors.errorName,new Date(this.otherTechnicalErrors[i].terrors.time),"other"));
            if(this.yLabels.indexOf(this.otherTechnicalErrors[i].terrors.errorName)==-1){
              this.yLabels2.push(this.otherTechnicalErrors[i].terrors.errorName);
            }
  
          }
  
          this.allErrorsf=this.allErrors.concat(this.allErrors2,this.allErrors3);
          this.allErrorsf.sort(function(a,b) { 
            return a.time.getTime() - b.time.getTime() 
          });
  
          for(var i=0;i<this.allErrorsf.length;i++){
            this.allErrorTimes.push(this.allErrorsf[i].time.toTimeString());
            if(this.allErrorsf[i].type=="transaction"){
              this.tedataset.push(this.allErrorsf[i].name);
              this.oedataset.push(null);
            }
            else{
              this.oedataset.push(this.allErrorsf[i].name);
              this.tedataset.push(null);
            }
          }
          this.yLabels2.push(" ");
          this.yLabels3=this.yLabels.concat(this.yLabels2);
  
        });
      });
      

      this.logService.getNoUserErrors(currentLog).subscribe(data=>{
        
         this.noUserErrors=data[0].count;
         if(this.noUserErrors>0){
          this.showUserErrors=true;
        }
        else{
          this.showUserErrors=false;
        }
       });

       this.logService.getUserErrorPoints(currentLog).subscribe(data=>{
        this.barLabels=[];
        this.barData=[];
        
         for(var i=0;i<data.length;i++){
          this.barLabels.push(data[i].x);
          this.barData.push(data[i].y);
         }
         
       });
      
      
    });
    
  }

}



