import { Component, OnInit } from '@angular/core';

import {AuthService} from '../../services/auth.service';

import {UsersService} from '../../services/users.service'
import {FlashMessagesService} from 'angular2-flash-messages';

@Component({
  selector: 'app-manage-users',
  templateUrl: './manage-users.component.html',
  styleUrls: ['./manage-users.component.css']
})
export class ManageUsersComponent implements OnInit {
  unApprovedUsers:Array<any>;
  approvedUsers:Array<any>;

  constructor(private authService: AuthService,private usersService: UsersService,private flashMessagesService:FlashMessagesService) { }

  ngOnInit() {
    this.setApprovedUsers();
    this.setunApprovedUsers();
  }

  setApprovedUsers(){
    this.usersService.getApprovedUsers().subscribe(data=>{
      
      this.approvedUsers=data;
    });
  }

  setunApprovedUsers(){
    this.usersService.getUnApprovedUsers().subscribe(data=>{
      this.unApprovedUsers=data;
    });
  }

  approveOnClick(user:any){
    this.usersService.approveUser(user).subscribe(data=>{
      if(data.success)
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-success',timeout:5000});
      else
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-danger',timeout:5000});
      this.setApprovedUsers();
      this.setunApprovedUsers();
    });
    
  }

  rejectOnClick(user:any){
    this.usersService.removeUser(user).subscribe(data=>{
      if(data.success)
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-success',timeout:5000});
      else
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-danger',timeout:5000});
      this.setApprovedUsers();
      this.setunApprovedUsers();
    });
  }

  resetPasswordOnClick(user:any){
    this.usersService.resetPassword(user.username,"pass123").subscribe(data=>{
      if(data.success)
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-success',timeout:5000});
      else
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-danger',timeout:5000});
      
    });
  }

}



