import { Component, OnInit } from '@angular/core';
import {UsersService} from '../../services/users.service';
import {FlashMessagesService} from 'angular2-flash-messages';
@Component({
  selector: 'app-manage-notifications',
  templateUrl: './manage-notifications.component.html',
  styleUrls: ['./manage-notifications.component.css']
})
export class ManageNotificationsComponent implements OnInit {
  title:string;
  body:string;
  notifications:Array<any>;
  hour:number;
  min:number;
  check:boolean;

  constructor(private usersService: UsersService,private flashMessagesService:FlashMessagesService) { }

  ngOnInit() {
    this.setNotifications();
    this.getNotificationTime();
  }
  onSend(){
    this.usersService.sendNotification(this.title,this.body).subscribe(data=>{
     
      if(data.success){
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-success',timeout:3000});
        this.title="";
        this.body="";
        this.setNotifications();
      }
      else{
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-danger',timeout:3000});
      }
    });
    
    this.setNotifications();
  }

  setNotifications(){
    
    this.usersService.getNotifications().subscribe(data=>{
      this.notifications=data.notf;
      for(var i=0;i<this.notifications.length;i++){
        this.notifications[i].date=new Date(this.notifications[i].date);
      }
    });
  }

  getNotificationTime(){
    this.usersService.getNotificationTime().subscribe(data=>{
      this.hour=data.hour;
      this.min=data.min;
      if(this.hour!=-1&&this.min!=-1){
        this.hour=this.hour+5;
        this.min=this.min+30;
        if(this.min>59){
          this.hour++;
          this.min=this.min-60;
        }
        if(this.hour>23){
          this.hour=this.hour-24;
        }
      }
      if(this.hour==-1){
        this.check=true;
      }
    });
  }

  setTime(){
    var h=this.hour-5;
    var m=this.min-30;
    if(m<0){
      m=60+m;
      h--;
    }
    if(h<0){
      h=24+h;
    }
    
    this.usersService.setNotificationTime(h,m).subscribe(data=>{
      console.log(data);
      if(data.success){
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-success',timeout:3000});
      }
      else{
        this.flashMessagesService.show(data.msg,{cssClass: 'alert-danger',timeout:3000});
      }
    });
  }

  checkTime(){
    if(this.hour>=0&&this.hour<24&&this.min>=0&&this.min<=59){
      this.check=false;
    }
    else{
      this.check=true;
    }
  }
}



