import { Injectable } from '@angular/core';
import {Http,Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

@Injectable()
export class LogService {
  private messageSource = new BehaviorSubject<string>("ATM00120170101");
  currentLog=this.messageSource.asObservable();
  test:number;

  constructor(private http:Http) { }
  
  changeLog(logName:string){
    this.messageSource.next(logName);
    
  }

  saveLog(text:String){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"text":text};
    return this.http.post('logdata/savelog',body,{headers: headers}).map(res=>res.json());
  }

  getLogNames(){
    let headers = new Headers();
    return this.http.get('logdata/lognames',{headers: headers}).map(res=>res.json());    
  }

  getLogLocations(){
    let headers = new Headers();
    return this.http.get('logdata/locations',{headers: headers}).map(res=>res.json());
  }

  getLogNamesByLocation(location:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"location":location};
    return this.http.post('logdata/lognamesbylocation',body,{headers: headers}).map(res=>res.json());
  }

  getLogNamesByLocationDate(date:string,location:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"date":date,"location":location};
    return this.http.post('logdata/lognamesbylocationdate',body,{headers: headers}).map(res=>res.json());
  }

  getLogNamesByDate(date:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"date":date};
    return this.http.post('logdata/lognamesbydate',body,{headers: headers}).map(res=>res.json());
  }

  getNoSuccessTransactions(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/nosucesstransactions',body,{headers: headers}).map(res=>res.json());
  }

  getNoFailedTransactions(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/nofailedtransactions',body,{headers: headers}).map(res=>res.json());
  }

  getTransactionSummaryByType(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/transactionsummary',body,{headers: headers}).map(res=>res.json());
  }

  getTransactionTimesGraph(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/transactiontimesgraph',body,{headers: headers}).map(res=>res.json());
  }

  getNoUserErrors(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/nousererrors',body,{headers: headers}).map(res=>res.json()); 
  }

  getNoTechnicalErrors(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/notechnicalerrors',body,{headers: headers}).map(res=>res.json()); 
  }

  getUpDown(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/updown',body,{headers: headers}).map(res=>res.json()); 
  }

  getLogInfo(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/loginfo',body,{headers: headers}).map(res=>res.json()); 
  }

  getTransactionTypeGraph(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/transactiontypegraph',body,{headers: headers}).map(res=>res.json());
  }

  getUserErrorPoints(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/usererrorpoints',body,{headers: headers}).map(res=>res.json());
  }

  getNoTransactionTechnicalErrors(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/notechnicaltransactionerrors',body,{headers: headers}).map(res=>res.json());
  }

  getTransactionTechnicalErrors(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/technicaltransactionerrors',body,{headers: headers}).map(res=>res.json());
  }

  getOtherTechnicalErrors(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/othertechnicalerrors',body,{headers: headers}).map(res=>res.json());
  }

  getTransactionValuesByTime(logName:string){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    const body={"logName":logName};
    return this.http.post('logdata/transactionamountsbytime',body,{headers: headers}).map(res=>res.json());
  }
}




