import { Injectable } from '@angular/core';
import {Http,Headers} from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class UsersService {
  authToken:any;
  constructor(private http:Http) { }

  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  getUnApprovedUsers(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    return this.http.get('users/unapprovedusers',{headers: headers}).map(res=>res.json());
  }

  getApprovedUsers(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    return this.http.get('users/approvedusers',{headers: headers}).map(res=>res.json());
  }

  approveUser(user:any){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    const body={"username":user.username,"usertype":user.requestusertype};
    return this.http.post('users/approveuser',body,{headers: headers}).map(res=>res.json());
  }

  resetPassword(username:string,password:string){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    const body={"username":username,"password":password};
    return this.http.post('users/resetpassword',body,{headers: headers}).map(res=>res.json());
  }

  removeUser(user:any){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    const body={"username":user.username};
    return this.http.post('users/deleteuser',body,{headers: headers}).map(res=>res.json());
  }

  sendNotification(title:string,nbody:string){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    const body={"title":title,"body":nbody};
    return this.http.post('notifications/sendnotifications',body,{headers: headers}).map(res=>res.json());
  }

  getNotifications(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    return this.http.get('notifications/getnotifications',{headers: headers}).map(res=>res.json());
  }

  setNotificationTime(hour:number,min:number){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    const body={"hour":hour,"min":min};
    return this.http.post('notificationsettings/setnotificationtime',body,{headers: headers}).map(res=>res.json());
  }

  getNotificationTime(){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization',this.authToken);
    headers.append('Content-Type','application/json');
    const body={};
    return this.http.post('notificationsettings/getnotificationtime',body,{headers: headers}).map(res=>res.json());
  }
}



