import { Component, OnInit } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { LogService } from '../../services/log.service'


class Point{
  x:Date;
  y:any;

  constructor(x:Date,y:any){
    this.x=x;
    this.y=y;
  }
}

class DataSet{
  label: string;
  borderColor:string;
  fill: boolean;
  data: Point[];

  constructor(label: string,borderColor: string,fill: boolean,data: Point[]){
    this.label=label;
    this.borderColor=borderColor;
    this.fill=fill;
    this.data=data;
  }
}

@Component({
  selector: 'app-day-view',
  templateUrl: './day-view.component.html',
  styleUrls: ['./day-view.component.css']
})


export class DayViewComponent implements OnInit {

  selLogName: string;
  successTransactions: number;
  failedTransactions: number;
  transactionsByType: Object[];
  ttgraph: Array<any>;
  noUserErrors: number;
  noTechnicalErrors: number;
  noTechnicalTransactionErrors:number;
  private chartData: Array<any>;
  updown: Object[];

  dateString : string="2017-01-01T";
  labelY1:string="No of Transactions";
  labelY2:string="State";
  sName1:string="Transactions";
  sName2:string="Status";

  gg:Array<Point>;
  uptime:Array<Point>=[];
  dataSet:Array<DataSet>=[];

  logDateFrom:string;
  logDateOFrom:Date;
  logDateTo:string;
  logDateOTo:Date;

  logUploadDate:string;
  atmId:string;
  location:string;
  deviceType:string;

  totalTAmount:number;


  constructor(private http: Http, private logService: LogService) {

  }

  ngOnInit() {
    
    this.logService.currentLog.subscribe(currentLog => {
      this.selLogName = currentLog;
      
      this.logService.getLogInfo(currentLog).subscribe(data=>{
        
        this.atmId=data[0].atmId;
        this.deviceType=data[0].deviceType;
        this.location=data[0].location;
        this.logDateFrom=new Date(data[0].timeValidFrom).toString();
        this.logDateOFrom=new Date(data[0].timeValidFrom);
        this.logDateTo=new Date(data[0].timeValidTo).toString();
        this.logDateOTo=new Date(data[0].timeValidTo);
        this.logUploadDate=new Date(data[0].dateUploaded).toString();
        
      });
      this.logService.getNoSuccessTransactions(currentLog).subscribe(data => {
        this.successTransactions = data[0].count;
        this.totalTAmount=data[0].amount;
         
      });

      this.logService.getNoFailedTransactions(currentLog).subscribe(data=>{
        this.failedTransactions=data[0].count;
      });
      this.logService.getTransactionSummaryByType(currentLog).subscribe(data => {
        this.transactionsByType = data;
      });
      this.logService.getTransactionTimesGraph(currentLog).subscribe(data => {
        this.ttgraph = data;
        this.gg=new Array(24);
        for(var i=0;i<24;i++){
          var lo=new Point(new Date(this.logDateOFrom.getFullYear(),this.logDateOFrom.getMonth(),this.logDateOFrom.getDate(),i,0,1),0);
          this.gg[i]=lo;
        }
        for(var i=0;i<this.ttgraph.length;i++){
          this.gg[this.ttgraph[i].x].y=this.ttgraph[i].y;
        }
      });
      this.logService.getNoUserErrors(currentLog).subscribe(data=>{
       
        this.noUserErrors=data[0].count;
      });

      this.logService.getNoTransactionTechnicalErrors(currentLog).subscribe(data=>{
        this.noTechnicalTransactionErrors=data[0].count;
      });

      this.logService.getNoTechnicalErrors(currentLog).subscribe(data=>{
        
        this.noTechnicalErrors=data[0].count;
      });
      this.logService.getUpDown(currentLog).subscribe(data=>{
        
        this.updown=data;
        
        this.uptime=[];
        for(var i=0;i<this.updown.length;i++){
          this.updown[i]['at']=new Date(this.updown[i]['at']);
        }
       

  
        var trigger=1;
        for(var k=this.logDateOFrom.getHours(),i=0;i<(this.logDateOTo.getTime()-this.logDateOFrom.getTime())/3600000;i++,k++){
          
            var l1=new Point(new Date(this.updown[0]['at'].getFullYear(),this.updown[0]['at'].getMonth(),this.updown[0]['at'].getDate(),k,0,0),trigger);
          

          for(var j=0;j<this.updown.length;j++){
            
            if(this.updown[j]['event']=="down"&&this.updown[j]['at'].getHours()==k){
              
              var l1=new Point(this.updown[j]['at'],0);
              var l2=new Point(new Date(l1.x.getTime()-1000),1)
              trigger=0;
              this.uptime.push(l2);
              this.uptime.push(l1);
            }
            if(this.updown[j]['event']=="up"&&this.updown[j]['at'].getHours()==k){
           
              
              var l1=new Point(this.updown[j]['at'],1);
              var l2=new Point(new Date(l1.x.getTime()-1000),0)
              trigger=1;
              this.uptime.push(l2);
              this.uptime.push(l1);
            }
            
          }
          this.uptime.push(l1);
        }
        this.dataSet=[];
        this.dataSet.push(new DataSet("State","#3e95cd",false,this.uptime));
          
      });

      

    });

    
  }

}



