import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {RouterModule, Routes} from '@angular/router';



import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { ProfileComponent } from './components/profile/profile.component';

import {ValidateService} from './services/validate.service';
import {AuthService} from './services/auth.service';
import {LogService} from './services/log.service';
import {UsersService} from './services/users.service';

import {FlashMessagesModule} from 'angular2-flash-messages';

import {AdminauthGuard} from './guards/adminauth.guard';
import {AuthGuard} from './guards/auth.guard';
import { DayViewComponent } from './components/day-view/day-view.component';


import {DashComponent} from './layouts/dash-layout/dash.component';
import {AuthComponent} from './layouts/auth-layout/auth.component';
import { LinechartComponent } from './components/shared/linechart/linechart.component';
import { TransactionsViewComponent } from './components/transactions-view/transactions-view.component';
import { BarchartComponent } from './components/shared/barchart/barchart.component';
import { ErrorsViewComponent } from './components/errors-view/errors-view.component';
import { ScatterchartComponent } from './components/shared/scatterchart/scatterchart.component';
import { ManageUsersComponent } from './components/manage-users/manage-users.component';
import { ManageNotificationsComponent } from './components/manage-notifications/manage-notifications.component';
import { UploadLogFileComponent } from './components/upload-log-file/upload-log-file.component';




const appRoutes: Routes = [
  {
    path:'',
    component: AuthComponent,
    children:[
      {path:'',component:HomeComponent},
      {path:'register',component:RegisterComponent},
      {path:'home',component:HomeComponent},
      {path:'login',component:LoginComponent}]
    },
    {
  path:'',
  component: DashComponent,
  children:[
    { path: 'summary', component: DayViewComponent,canActivate:[AuthGuard] },
    { path: 'transactionsview', component: TransactionsViewComponent,canActivate:[AuthGuard] },
    { path: 'errorsview', component: ErrorsViewComponent,canActivate:[AuthGuard] },
    {path:'profile',component:ProfileComponent, canActivate:[AuthGuard]},
    {path:'manageusers',component:ManageUsersComponent, canActivate:[AdminauthGuard]},
    {path:'managenotifications',component:ManageNotificationsComponent, canActivate:[AdminauthGuard]},
    {path:'uploadlog',component:UploadLogFileComponent, canActivate:[AdminauthGuard]},
  ]
}];
  
  
  
  
  /** 
  {path:'',component:HomeComponent},
  {path:'register',component:RegisterComponent},
  {path:'login',component:LoginComponent},
  {path:'dashboard',component:DashboardComponent,canActivate:[AuthGuard]},
  {path:'profile',component:ProfileComponent, canActivate:[AuthGuard]},
  { path: 'summary', component: DayViewComponent },
  { path: 'detailedview', component: DetailedViewComponent },
  { path: 'criticalerrors', component: CriticalErrorsViewComponent }
]
**/
@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    ProfileComponent,
    DayViewComponent,
    DashComponent,
    AuthComponent,
    LinechartComponent,
    TransactionsViewComponent,
    BarchartComponent,
    ErrorsViewComponent,
    ScatterchartComponent,
    ManageUsersComponent,
    ManageNotificationsComponent,
    UploadLogFileComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule
  ],
  providers: [ValidateService,AuthService,AuthGuard,AdminauthGuard,LogService,UsersService],
  bootstrap: [AppComponent]
})
export class AppModule { }



